<template>
  <container :title="title">
    <list :show-del="true">
      <thead slot="thead">
      <tr>
        <th><checkbox v-model="checked" :checked="tgCheck"/></th>
        <th>{{lang.id}}</th>
        <th>{{lang.title}}</th>
        <th>{{lang.typename}}</th>
        <th>{{lang.username}} - {{lang.uid}}</th>
        <th>{{lang.click}}</th>
        <th>{{$t('status.createTime')}}</th>
        <th>{{$t('status.updateTime')}}</th>
        <th>{{lang.status}}</th>
        <th>{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="vol in list">
        <td><checkbox  v-model="checkbox" :checked="checked" :value="vol.id"/></td>
        <td>{{vol.id}}</td>
        <td>{{vol.title}}
          <span v-if="vol.flag" style="margin: 0 3px" class="label label-info"
                v-for="flag in makeFlag(vol.flag)">
            {{$t('flags.'+flag )}}</span>
        </td>
        <td>{{vol.typename}}</td>
        <td>{{vol.username}} - {{vol.uid}}</td>
        <td>{{vol.click}}</td>
        <td>{{vol.create_time}}</td>
        <td>{{vol.edit_time}}</td>
        <v-td type="status" :value="vol.status" />
        <v-td type="opt" :value="vol.id"
              :jumpArchive="vol"
              edit-route="article_create_edit" />
      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List,Checkbox, VTd} from "@/components"

  export default {
    components: {VTd, List, Container,Checkbox},
    data(){
      return{
        title:this.$t('archive.index'),
        lang:this.$t('archive'),
        checked:false,
        tgCheck:false,
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      },
      checkbox:{
        get(){
          return this.$store.state.table.checkbox
        },
        set(value){
          this.$store.commit('table/SET_CHECKBOX',value)
        }
      }
    },
    watch:{
      checkbox(newValue){
        const length = newValue.length
        if (length === this.list.length){
          this.tgCheck = true
        }
        if (length === 0) {
          this.tgCheck = false
        }
      }
    },
    methods:{
      makeFlag(flag){
        let ret = []
        if (flag !== undefined){
          ret = flag.split(',')
        }
        return ret
      }
    },
    mounted(){
      const url = '/api/archive/index'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



